import React from "react";
import * as domtoimage from "dom-to-image";
import "./InteractionMenu.styles.css";

export default function InteractionMenu({ set_linkURL }) {
  let node = document.querySelector("#img-section");

  function saveCanvas() {
    node.firstChild.style.borderRadius = "0px";
    node.querySelector("#SVG").style.borderRadius = "0px";
    setTimeout(function () {
      domtoimage
        .toPng(node)
        .then((dataUrl) => {
          const img = new Image();
          img.src = dataUrl;
          img.setAttribute("class", "img-to-download");
          let link_to_download_img = document.createElement("a");
          link_to_download_img.href = img.src;
          link_to_download_img.setAttribute("download", "your_image");
          document.body.append(img);
          document.body.append(link_to_download_img);
          link_to_download_img.click();
          node.firstChild.style.borderRadius = "25px";
          node.querySelector("#SVG").style.borderRadius = "25px";
          document.body.removeChild(img);
          document.body.removeChild(link_to_download_img);
        })
        .catch((error) => console.error("oops, something went wrong!", error));
    }, 1000);
  }
  return (
    <div className="interaction-menu">
      {/* <div className="URL-search">
        <input
          type="text"
          onChange={(e) => {
            set_linkURL(e.target.value);
          }}
        />
      </div> */}
      <div className="attach-file">
        <input
          style={{ display: "none" }}
          type="file"
          accept=".jpg, .jpeg, .png"
          id="myfile"
          name="myfile"
          onChange={(e) => {
            if (document.querySelector("#myfile").files.length != 0) {
              set_linkURL(URL.createObjectURL(e.target.files[0]));
            }
          }}
        />
      </div>
      <div className="download-btn">
        <button
          onClick={() => {
            saveCanvas();
          }}
        >
          Скачать изображение
        </button>
      </div>
    </div>
  );
}
